/*global analytics */

import { useEffect } from 'react';
import '../App.css';
import './Dawn.css';
import AppleMusicLogo from '../assets/applemusic_logo.svg';
import SpotifyLogo from '../assets/spotify_logo.svg';
import AmazonMusicLogo from '../assets/amazonmusic_logo.svg';
import ITunesLogo from '../assets/itunes_logo.svg';
import JioSaavnLogo from '../assets/jiosaavn_logo.svg';
import YouTubeLogo from '../assets/youtube_logo.svg';
import YouTubeMusicLogo from '../assets/youtubemusic_logo.svg';

const links = {
	'applemusic': 'https://music.apple.com/us/album/dawn/1573009973',
	'spotify': 'https://open.spotify.com/album/6PMPBNcCk1moirs7suGbYv',
	'amazonmusic': 'https://music.amazon.in/albums/B097HR5FRG',
	'itunes': 'https://music.apple.com/in/album/dawn/1573009973',
	'jiosaavn': 'https://www.jiosaavn.com/album/dawn/EEUXyq3oaY0_',
	'youtube': 'https://www.youtube.com/watch?v=z35NbXSRl4Y&list=OLAK5uy_mtxCisevcILKStAdaxCq_6FklNxXeLBPg',
	'youtubemusic': 'https://music.youtube.com/playlist?list=OLAK5uy_l61abOzPHq0Id_ZdGT77O21A__4xXui7c'
}

const MusicService = (props) => {

	const openLink = (id) => {
		let url = links[id];
		analytics.track('Opened music service', {service: id});
		window.open(url, '_self');
	}


	return (
        <div onClick={() => {openLink(props.id)}} className="music-service">
    		<div>
    			<div style={{margin: '5px', height: '40px', width: '125px', backgroundPosition: 'center', background: `url(${props.src})`, backgroundRepeat: 'no-repeat', backgroundSize: 'cover' }}></div>
    		</div>
    		<div style={{lineHeight: '45px'}}>
    			<button className="button-secondary">{props.buttonLabel}</button>
    		</div>
    	</div>
    );
}

function Dawn() {

	useEffect(() => {

		//analytics.track(event, properties, options, callback);
		analytics.track('Loaded Dawn Page', {});

	}, []);


    return (
        <div className="music-list-wrapper">
            <div className="artwork"></div>
            <div className="heading">
            	<h1>Sanjeev Pande - Dawn</h1>
            	<p>Choose music service</p>
            </div>
            <div className="music-services-wrapper">
            	<MusicService id="applemusic" buttonLabel="Play" src={AppleMusicLogo} />
            	<MusicService id="spotify" buttonLabel="Play" src={SpotifyLogo} />
            	<MusicService id="amazonmusic" buttonLabel="Play" src={AmazonMusicLogo} />
            	<MusicService id="itunes" buttonLabel="Buy" src={ITunesLogo} />
            	<MusicService id="jiosaavn" buttonLabel="Play" src={JioSaavnLogo} />
            	<MusicService id="youtube" buttonLabel="Watch" src={YouTubeLogo} />
            	<MusicService id="youtubemusic" buttonLabel="Play" src={YouTubeMusicLogo} />
            </div>
        </div>
    );
}

export default Dawn;
